    <img class="room-img" v-bind:src=room.image>
    <h4 @click="$emit('openModal',room.id)">{{ room.title }}</h4>
    <p>{{ room.price }}원</p>
    <button @click="$emit('신고',room.id)">허위매물신고</button>
    <span>신고수: {{ 신고수[room.id] }}</span>
  </div>
</template>

<script>
export default {
     name : 'CardVue',
     props : {
        room : Object,
        신고수: Array,
        
     }
}
</script>

